export default function (orders = [], action) {
    switch (action.type) {
        case "SET_ORDERS":
            return [...action.payload];
        case "ADD_ORDER":
            return [...orders, action.payload];
        case "ORDERS_CHANGED":
            for (let i = 0; i < action.payload.length; i++) {
                const res = action.payload[i];
                const item = orders.find(x => x.id === res.id);
                if (item) {
                    orders[orders.indexOf(item)] = { ...item, ...res };
                    continue;
                }
                if (!item) orders.push(res);
            }
        default:
            return orders;
    }

}