import React from "react";
import { Drawer, List, ListItem, ListItemText, ListItemIcon, Divider, Hidden, IconButton } from "@material-ui/core";
import { Link } from "react-router-dom";
import { Close } from "@material-ui/icons";
export default function (props) {
    const { STEPS, current_step, classes, window, onCloseMenu, isMenuOpen } = props;
    const container = window !== undefined ? () => window().document.body : undefined;

    const MenuContent = (<List>
        {STEPS.map((step, index) => (<React.Fragment key={index}>
            {step.addDividerBefore && <Divider />}
            <ListItem
                component={Link}
                to={"/" + step.id}
                selected={step.id === current_step}
                button
            >
                <ListItemText>{step.title}</ListItemText>
                <ListItemIcon>{<step.Icon />}</ListItemIcon>
            </ListItem>
        </React.Fragment>
        ))}
    </List>);

    return (<nav className={classes.drawer}>

        <Hidden smUp implementation="css" >
            <Drawer
                container={container}
                variant="temporary"
                anchor={'left'}
                open={isMenuOpen}
                onClose={onCloseMenu}
                classes={{ paper: classes.drawerPaper }}
                ModalProps={{
                    keepMounted: true, // Better open performance on mobile.
                }}
            >
                <div className={classes.toolbar} style={{ textAlign: "right" }}>
                    <IconButton onClick={onCloseMenu}>
                        <Close />
                    </IconButton>
                </div>
                {MenuContent}
            </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
            <Drawer
                classes={{ paper: classes.drawerPaper }}
                variant="permanent"
                open
            >
                <div className={classes.toolbar}></div>
                {MenuContent}
            </Drawer>
        </Hidden>
    </nav>);
}