import React from "react";
import { Typography, Button } from "@material-ui/core";

export default function (props) {
    return (<Button fullWidth onClick={props.onClick} component="button" value={props.id} variant="outlined" style={{ margin: 5, textAlign: "left" }} >
        <div style={{ display: "table-cell", width: "100%" }}>
            <div style={{ display: "table-row" }}>
                <div style={{ display: "table-cell" }}>
                    <Typography style={{ display: "table-cell", width: "100%" }} variant="h6">{props.nombre}</Typography>
                </div>
                <div style={{ display: "table-cell" }}>
                    <Typography style={{ display: "table-cell", whiteSpace: "nowrap" }}>
                        # {props.numero}
                    </Typography>
                </div>
            </div>
            <div style={{ display: "table-row" }}>
                <div style={{ display: "table-cell", width: "100%" }}>
                    <Typography variant="button">
                        {new Date(props.createdAt).asLocal()}
                    </Typography>
                </div>
                <div style={{ display: "table-cell", whiteSpace: "nowrap" }}>
                    <Typography>
                        $ {props.total}
                    </Typography>
                </div>
            </div>
        </div>
    </Button >);
}