export function SetOrders(payload) {
    return {
        type: "SET_ORDERS",
        payload
    }
}

export function AddOrder(payload) {
    return {
        type: "ADD_ORDER",
        payload
    }
}
export function OrdersChanged(payload) {
    return {
        type: "ORDERS_CHANGED",
        payload
    }
}

export function SetOrderStatus(payload: "LOADING" | "ERROR" | "") {
    return {
        type: "SET_ORDER_STATUS",
        payload
    }
}