export function NotifyOrderChanged(order: any) {
    if (Notification.permission !== "granted") return;

    var title = "LA ORDEN #" + order.numero;
    if (order.estado === 0) title = "NUEVO PEDIDO";
    if (order.estado === 1) title += " ESTÁ EN RUTA";
    if (order.estado === 2) title += " FUE ENTREGADA";
    if (order.estado === 3) title += "FUE CANCELADA";

    navigator.serviceWorker.ready.then(function (registration) {
        registration.showNotification(title, {
            body: order.nombre + (order.estado === 0 ? " Acaba de realizar un pedido" : " - Click para ver el pedido"),
            icon: "./favicon.ico",
            data: order.id
        });
    });
}