import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Login from './Login';
import API, { viewLoginData, storeLoginData } from "./Services/API";
import ReduxStore from './redux/store';
import { SET_TIENDA, SET_POSITION } from './redux/actions/Store';

function LaunchApp(data?) {
    // uso viewLoginData porque pone por default la position en quito
    // en caso de que position sea NULL
    if (!data) data = viewLoginData();
    ReduxStore.dispatch(SET_TIENDA(data.store));
    ReduxStore.dispatch(SET_POSITION(data.position));
    API.setStoreId(data.store.nombre);

    ReactDOM.render(
        <App user={data.user} store={ReduxStore} onLogOut={LoggedOut} />
        , document.getElementById('app')
    );
}
function LaunchLogin() {
    ReactDOM.render(<React.StrictMode>
        <Login onLogged={Logged} />
    </React.StrictMode>, document.getElementById('app'));
}
function Logged(data) {
    storeLoginData(data);
    LaunchApp();
}
function LoggedOut() {
    API.STOP_LISTENING();
    storeLoginData();
    LaunchLogin();
}

viewLoginData() ? LaunchApp() : LaunchLogin();

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
//register();
/*
ServiceWorker.register();
*/
try {
    navigator.serviceWorker && navigator.serviceWorker.register("./service-worker.js");
} catch (ex) {
    console.error("Service Worker wont update", ex);
}
