import React from "react";
import { Typography, Button } from "@material-ui/core";
import OrderItem from "./OrderItem";
import { ArrowDropDown } from "@material-ui/icons";



export default function (props) {
    const [visible, setVisible] = React.useState(props.openedByDefault === true);
    const { data, onClick, label } = props;
    const style: any = { animation: "rotation 2s linear" };
    if (!visible) style.transform = "rotate(180deg)";
    return (
        <div style={{ maxWidth: 600, margin: "auto" }}>
            <Button fullWidth variant="text" type="button" onClick={event => setVisible(!visible)}>
                <Typography variant="h6" >{label}</Typography>
                <ArrowDropDown style={style} />
            </Button>
            <div style={{ display: visible ? "block" : "none" }}>
                {data.map(o => (<OrderItem key={o.id} {...o} onClick={onClick} />))}
            </div>
        </div>);
}