import React from "react";
import { CircularProgress, Typography, Dialog, IconButton } from "@material-ui/core";
import OrderDetails from "./OrderDetails";
import { Route } from "react-router-dom";
import { connect } from "react-redux";
import API from "../../Services/API";
import { SetOrders, SetOrderStatus, OrdersChanged } from "../../redux/actions/orders";
import { Close } from "@material-ui/icons";
import OrderGroup from "./OrderGroup";

class Orders extends React.Component<{
    history: any;
    match: any;
    data: any[];
    SetOrders: any;
    SetOrderStatus: any;
    OrdersChanged: any;
    status: string;
}> {
    state = {
        now: new Date().valueOf(),
        error: null
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);
        this.handleClick = this.handleClick.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleChanged = this.handleChanged.bind(this);
    }
    componentWillMount() {
        this.props.SetOrderStatus("LOADING");
        API.GetOrders()
            .then(this.props.SetOrders)
            .then(() => this.props.SetOrderStatus(""))
            .catch(error => {
                this.props.SetOrderStatus("ERROR");
                this.setState({ error });
            });
    }
    componentWillUnmount() {
        this.props.SetOrderStatus("");
    }
    handleClick(event) {
        const id = event.currentTarget.value;
        this.props.history.push(this.props.match.path + "/" + id);
    }
    handleClose() {
        this.props.history.push(this.props.match.path);
    }
    handleChanged(changes) {
        this.props.OrdersChanged(changes);
        this.handleClose();
    }

    render() {
        const { error } = this.state;
        const { status, data } = this.props;

        if (status === "LOADING") return (<div style={{ width: "100%", textAlign: "center", margin: "auto", paddingTop: 100 }}>
            <CircularProgress size={70} />
            <Typography variant="h4" >CARGANDO...</Typography>
        </div>);
        if (error) return (<div style={{ width: "100%", textAlign: "center", margin: "auto", paddingTop: 100 }}>
            <Typography variant="h4">Ocurrió un error</Typography>
            <Typography variant="subtitle1">{error.message}</Typography>
            <Typography variant="body1">{error.status} | {error.statusText} | {error.code}</Typography>
        </div>);

        const { pendientes, enRuta, entregados } = data.reduce((a, b) => {
            if (b.estado === 0) a.pendientes.push(b);
            if (b.estado === 1) a.enRuta.push(b);
            if (b.estado === 2) a.entregados.push(b);
            return a;
        }, {
            pendientes: [],
            enRuta: [],
            entregados: []
        });

        return (<div style={{ display: "flex", flexDirection: "row", height: "100%" }}>
            <div style={{ flex: 1, overflow: "auto" }}>
                <OrderGroup openedByDefault={true} label="PENDIENTES" data={pendientes} onClick={this.handleClick} />
                <OrderGroup openedByDefault={false} label="EN RUTA" data={enRuta} onClick={this.handleClick} />
                <OrderGroup openedByDefault={false} label="ENTREGADOS" data={entregados} onClick={this.handleClick} />
            </div>
            <Route path={`${this.props.match.path}/:id`}
                children={({ match, history }) => (<Dialog fullScreen onClose={this.handleClose} open={Boolean(match && match.params.id)} >
                    <IconButton style={{ position: "absolute", right: 20 }} onClick={this.handleClose} type="button">
                        <Close />
                    </IconButton>
                    {(match && match.params.id)
                        ? <OrderDetails
                            key={match && match.params.id}
                            onEstadoChanged={this.handleChanged}
                            history={history}
                            id={match && match.params.id}
                        />
                        : null}
                </Dialog>)}
            />
        </div>);
    }
}

const mapStateToProps = state => ({ data: state.orders, status: state.order_status });
const mapDispatchToProps = {
    SetOrders,
    SetOrderStatus,
    OrdersChanged
}
export default connect(mapStateToProps, mapDispatchToProps)(Orders);