import React from "react";
import { AppBar, Toolbar, Typography, IconButton, MenuItem, Hidden, Menu } from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";
import MenuIcon from "@material-ui/icons/Menu";

export default class extends React.PureComponent<{
    [key: string]: any
}> {
    state = {
        menuEl: null
    }
    constructor(props) {
        super(props);
        this.handleMenu = this.handleMenu.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleLogOut = this.handleLogOut.bind(this);
    }
    handleMenu(event) {
        this.setState({ menuEl: event.currentTarget });
    }
    handleClose() {
        this.setState({ menuEl: null });
    }
    handleLogOut() {
        var ok = window.confirm("Está seguro de cerrar sesión?");
        if (ok) this.props.onLogOut();
        this.handleClose();
    }
    render() {
        const { title, store, user, onMenuClick } = this.props;
        const { menuEl } = this.state;
        const open = Boolean(menuEl);

        return (<AppBar position="fixed" style={{ zIndex: 1201 }}>
            <Toolbar style={{ display: "flex", flexDirection: "row" }}>
                <Hidden smUp implementation="css">
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={onMenuClick}
                    >
                        <MenuIcon />
                    </IconButton>
                </Hidden>
                <div style={{ flexGrow: 1 }}>
                    <Hidden xsDown implementation="css">
                        <Typography style={{ textTransform: "uppercase" }} variant="h4" color="inherit" >{title || ""}</Typography>
                    </Hidden>
                    <Hidden smUp implementation="css">
                        <Typography style={{ textTransform: "uppercase" }} variant="h6" color="inherit" >{title || ""}</Typography>
                    </Hidden>
                </div>

                <Hidden smDown implementation="css">
                    <div style={{ display: "block", textAlign: "right" }}>
                        <Typography style={{ display: "block" }} color="inherit" variant="button">{(store.titulo || "").toUpperCase()}</Typography>
                        <Typography style={{ display: "block" }} color="inherit" variant="caption">{user}</Typography>
                    </div>
                </Hidden>

                <div>
                    <IconButton
                        title={store.titulo}
                        aria-owns={open ? 'menu-appbar' : undefined}
                        aria-haspopup="true"
                        onClick={this.handleMenu}
                        color="inherit"
                    >
                        <AccountCircle />
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={menuEl}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        open={open}
                        onClose={this.handleClose}
                    >
                        <MenuItem style={{ minWidth: 150 }} onClick={this.handleLogOut}>Salir</MenuItem>
                    </Menu>
                </div>
            </Toolbar>
        </AppBar>)
    }
};