export default class {
    static currentPosition() {
        return new Promise((resolve, reject) => {
            console.info("obteniendo posicion actual con navigator.geolocation.getCurrentPosition");
            if (!navigator.geolocation) {
                console.info("navigator.geolocation.getCurrentPosition no existe en este navegador, intentando con PositionByIP()");
                return PositionByIP().then(resolve).catch(reject);
            }
            navigator.geolocation.getCurrentPosition(
                geoLocation => resolve({
                    lat: geoLocation.coords.latitude,
                    lng: geoLocation.coords.longitude
                }), () => {
                    console.info("navigator.geolocation.getCurrentPosition falló, intentando con PositionByIP()");
                    PositionByIP().then(resolve).catch(reject);
                }
            );
        });
    }
    static QUITO_BOUNDS = {
        west: -78.85300,
        north: -0.04875,
        east: -78.17184,
        south: -0.39207
    }
    static QUITO = {
        lat: -0.189515,
        lng: -78.488167
    }
    static GoogleMapsRouteURL(currentLocation, locations) {
        const rutas = [currentLocation];
        for (let i = 0; i < locations.length; i++) {
            rutas.push(locations[i]);
        }
        if (rutas.length === 1) return "";
        return "https://www.google.com/maps/dir/ " +
            rutas.map(x => [x.lat, x.lng].join(",")).join("/");
    }
}

function PositionByIP() {
    return fetch("https://ipapi.co/json/").then(x => x.json())
        .then(info => ({ lat: info.latitude, lng: info.longitude }));
}