const alertInterval = {
    id: null,
    intervalId: null
};

const AlertMiddleware = store => next => action => {
    if (action.type === "NEW_ORDER") {
        if (alertInterval.intervalId) clearInterval(alertInterval.intervalId);
        alertInterval = {
            intervalId: setInterval(() => document.getElementById("order-alert").play(), 1000),
            id: action.payload.id
        };
    }
    if (action.type === "ORDER_OPENED" && alertInterval) {
        if (alertInterval) clearInterval(alertInterval.intervalId);
    }
    return next(action);
}
export default AlertMiddleware;