import React from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import { Typography, Paper, withStyles, Hidden } from "@material-ui/core";
import { Inbox, Category, Settings/*, Assessment*/ } from "@material-ui/icons";
import AppBar from "./Layouts/Bar";
import AppMenu from "./Layouts/Menu";
import Orders from "./Components/Orders/index";
import API from "./Services/API";
import Products from "./Components/Products";
import Config from "./Components/Config";
import { Provider } from "react-redux";

const STEPS = [
  { id: "orders", title: "Pedidos", Icon: Inbox },
  { id: "products", title: "Productos", Icon: Category },
  //{ id: "dashboard", title: "Dashboard", Icon: Assessment },
  { id: "config", title: "Configuración", Icon: Settings, addDividerBefore: true }
];

const drawerWidth = 200;
export default withStyles(theme => ({
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    [theme.breakpoints.up('sm')]: {
      marginLeft: drawerWidth
    }
  },
  appBar: {
    display: "block"
  }
}))(class App extends React.Component<{ store: any, user: any, onLogOut: any, classes?: any, window?: any }> {
  state = { current_step: "", isMenuOpen: false }
  constructor(props) {
    super(props);
    this.handleMounted = this.handleMounted.bind(this);
    this.onToggleMenu = this.onToggleMenu.bind(this);
    API.START_LISTENING();
  }
  REMOVE_LISTENER;
  componentDidMount() {
    if (!window.Notification) return;
    Notification.requestPermission()
      .then(p => console.info("Notification status", p))
      .catch(console.error);
  }
  componentWillUnmount() {
    this.REMOVE_LISTENER && this.REMOVE_LISTENER();
  }
  handleMounted(current_step) {
    this.setState({ current_step, isMenuOpen: false });
  }
  onToggleMenu() {
    this.setState({ isMenuOpen: !this.state.isMenuOpen });
  }
  render() {
    const { current_step, isMenuOpen } = this.state;
    const { title } = current_step && STEPS.find(x => x.id === current_step);
    const tienda = this.props.store.getState();

    const { classes, window } = this.props;

    return (<HashRouter>
      <Provider store={this.props.store}>
        <Paper style={{ height: "100vh", width: "100%", display: "flex", flexDirection: "column" }}>
          <AppBar
            user={this.props.user}
            store={tienda.store}
            title={title}
            onLogOut={this.props.onLogOut}
            onMenuClick={this.onToggleMenu}
          />
          <AppMenu
            STEPS={STEPS}
            current_step={current_step}
            classes={classes}
            window={window}
            isMenuOpen={isMenuOpen}
            onCloseMenu={this.onToggleMenu}
          />


          <main className={classes.content}>
            <div className={classes.toolbar}></div>
            <Switch>

              <Route path="/orders" render={props => (<Orders
                {...props}
                onMounted={this.handleMounted}
                stepID={"orders"}
              />)} />

              <Route path="/products" render={props => (<Products
                {...props}
                onMounted={this.handleMounted}
                stepID={"products"}
              />)} />

              <Route path="/config" render={props => (<Config
                {...props}
                onMounted={this.handleMounted}
                stepID={"config"}
              />)} />

              <Route exact path="/" >
                <div style={{ paddingTop: 100, width: "100%", textAlign: "center" }}>
                  <Typography variant="h4">BIENVENIDO!</Typography>
                  <Typography variant="subtitle2">RunFood Orders - Administrador</Typography>
                </div>
              </Route>
            </Switch>
          </main>
        </Paper>
      </Provider>
    </HashRouter>)
  }
});