import io from "socket.io-client";
import ReduxStore from "../redux/store";
import { OrdersChanged } from "../redux/actions/orders";
import { NotifyOrderChanged } from "./Notifications/index";
/*const URL = (window.location.href.indexOf("http://localhost") === 0)
    ? "http://localhost:10000/data/"
    : (window.location.protocol === "https:"
        ? "https://orders-runfoodapp.rj.r.appspot.com/data/"
        : "http://runfoodapp.ddns.net:10000/data/");*/
const URL = (window as any).PROJECT_ENV === "development"
    ? "http://localhost:10000"
    : "https://www.orders.runfoodapp.com";

const QUITO = { lat: -0.180653, lng: -78.467834 };

const session = new Date().toString() + ":" + Math.floor(100000 + Math.random() * 900000);

export function storeLoginData(info?) {
    if (info) {
        window.localStorage.setItem("__", JSON.stringify(info));
    } else {
        window.localStorage.removeItem("__");
    }
}
export function viewLoginData() {
    let info: any = window.localStorage.getItem("__");
    if (!info) return;
    info = JSON.parse(info);
    if (!info.position) info.position = QUITO;
    return info;
}
function getToken() {
    const info = viewLoginData();
    if (!info) {
        window.location.reload();
        throw { message: "Necesitas iniciar sesión para entrar aquí." };
    }
    return info.token + "+" + session;
}

let socket;
class API {
    private storeID: string;
    setStoreId(Id: string) {
        this.storeID = Id;
    }
    LogIn(data) {
        return Fetch(`${URL}/login`, {
            body: JSON.stringify(data),
            method: "POST"
        }).catch(x => {
            if (x.status === 400) throw { ...x, message: "Usuario o contraseña incorrectos." };
            if (x.status === 401) throw { ...x };
            if (x.status === 401) throw { ...x };
            throw x;
        });
    }

    GetOrders(body?) {
        return Fetch(`${URL}/data/${this.storeID}/order?${new URLSearchParams(body).toString()}`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    GetOrder(ID) {
        return Fetch(`${URL}/data/${this.storeID}/order/${ID}`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    OrdersOnRoute(orders) {
        return Fetch(`${URL}/data/${this.storeID}/order-actions/ON-ROUTE`, {
            method: "POST",
            body: JSON.stringify({ orders }),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    OrdersDelivered(orders) {
        return Fetch(`${URL}/data/${this.storeID}/order-actions/DELIVERED`, {
            method: "POST",
            body: JSON.stringify({ orders }),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }

    GetGroups() {
        return Fetch(`${URL}/data/${this.storeID}/group?admin_info=true`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    SaveGroup(group) {
        const ID = group.id;
        return Fetch(`${URL}/data/${this.storeID}/group${ID ? `/${ID}` : ""}`, {
            method: ID ? "PUT" : "POST",
            body: JSON.stringify(group),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        })
    }
    RequestActionGroup(group) {
        return Fetch(`${URL}/data/${this.storeID}/group/${group.id}`, {
            method: "PATCH",
            body: JSON.stringify(group),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        })
    }
    DeleteGroup(group) {
        return Fetch(`${URL}/data/${this.storeID}/group/${group.id}`, {
            method: "DELETE",
            body: JSON.stringify(group),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }

    GetProduct(ID) {
        return Fetch(`${URL}/data/${this.storeID}/product/${ID}?admin_info=true`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    SaveProduct(product) {
        const ID = product.id && product.id !== "NUEVO" && product.id;
        return Fetch(`${URL}/data/${this.storeID}/product${ID ? `/${ID}` : ""}`, {
            method: ID ? "PUT" : "POST",
            body: JSON.stringify(product),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        })
    }
    RequestActionProduct(product) {
        return Fetch(`${URL}/data/${this.storeID}/product/${product.id}`, {
            method: "PATCH",
            body: JSON.stringify(product),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        })
    }
    DeleteProduct(product) {
        return Fetch(`${URL}/data/${this.storeID}/product/${product.id}`,
            {
                method: "DELETE",
                body: JSON.stringify(product),
                headers: {
                    "Authorization": "Basic " + btoa(getToken())
                }
            })
    }

    GetParamContactos() {
        return Fetch(`${URL}/data/${this.storeID}/config?items=contactos`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    GetConfigInfo() {
        return Fetch(`${URL}/data/${this.storeID}/config`, {
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }
    setConfigInfo(info_item) {
        return Fetch(`${URL}/data/${this.storeID}/config`, {
            method: "POST",
            body: JSON.stringify(info_item),
            headers: {
                "Authorization": "Basic " + btoa(getToken())
            }
        });
    }

    START_LISTENING() {
        socket = io(URL, {
            query: {
                token: btoa(getToken())
            },
            reconnection: true,
            transports: ["polling"]
        });
        console.info("SOCKET CONNECTED");
        socket.on("ORDERS_CHANGED", ({ payload, origin }) => {
            if (origin === btoa(getToken())) return;
            ReduxStore.dispatch(OrdersChanged(payload));
            try {
                for (let i = 0; i < payload.length; i++) {
                    const order = payload[i];
                    NotifyOrderChanged(order);
                }
            } catch (ex) {
                console.error("Notification error", ex);
            }
        });
    }
    STOP_LISTENING() {
        socket && socket.disconnect();
        console.info("SOCKET DISCONNECTED");
    }
}

function Fetch(URI, options) {
    const DEFAULTS = {
        method: "GET",
        cache: "reload",
        mode: "cors",
        headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
        }
    };
    // headers
    if (options && options.headers) {
        for (const key in options.headers) {
            DEFAULTS.headers[key] = options.headers[key];
        }
        delete options.headers;
    }
    return fetch(URI, { ...DEFAULTS, ...options }).then(response => {
        if (response.ok) {
            return response.json();
        } else {
            return response.clone().json()
                .catch(() => {
                    return response.text().then(x => {
                        throw {
                            message: x,
                            status: response.status,
                            statusText: response.statusText
                        }
                    })
                }).then(obj => {
                    throw {
                        ...obj,
                        status: response.status,
                        statusText: response.statusText
                    }
                })
        }
    }).catch(ex => {
        if (ex.message === "Failed to fetch") {
            throw {
                message: "Este servicio no se encuentra disponible por el momento. Intentelo más tarde.",
                status: 0
            }
        }
        if (!ex.message) ex.message = ex.statusText;
        throw ex;
    });
}
export default new API();

