import React from "react";
import {
    DialogContent, CircularProgress, Typography, Divider,
    DialogActions, Button, TextField
} from "@material-ui/core";
import { PhoneOutlined, BusinessOutlined, LocationOnOutlined, Send } from "@material-ui/icons";
import API from "../../Services/API";
import OrderToPhone from "../../Services/WhatsApp";
import { connect } from "react-redux";
import Geo from "../../Services/Geo";

class OrderDetails extends React.Component<{
    history: any, id?: string, porcentajeIVA: number, show: boolean, onEstadoChanged: any, position: any
}> {
    state = {
        data: null,
        error: null,
        phone: "",
        status: "",
        contactos: null
    }
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.handlePhone = this.handlePhone.bind(this);
    }
    handlePhone(event) {
        this.setState({ phone: event.currentTarget.value });
    }
    handleClick() {
        if (!this.state.data) return;
        if (this.state.status) return;
        this.setState({ status: "LOADING" });
        let promise;
        if (this.state.data.estado === 0) promise = API.OrdersOnRoute([this.state.data.id]);
        if (this.state.data.estado === 1) promise = API.OrdersDelivered([this.state.data.id]);
        if (!promise) return this.setState({ status: "" });
        promise.then(this.props.onEstadoChanged)
            .catch(error => this.setState({ status: "", error }));
    }

    componentWillMount() {
        Promise.all([
            API.GetOrder(this.props.id),
            API.GetParamContactos()
        ]).then(([data, contactos]) => this.setState({ data, contactos, error: null })
        ).catch(error => this.setState({ error }));
    }

    render() {
        const porcentajeIVA = this.props.porcentajeIVA || 12;
        const { data, error, phone, contactos, status } = this.state;
        if (!data) return (<DialogContent>
            {error
                ? (<div style={{ textAlign: "center", padding: 20 }}>
                    <Typography variant="h6">Ocurrió un error</Typography>
                    <Typography variant="body2">{error.message}</Typography>
                    <Typography variant="button">{error.status} {error.statusText}</Typography>
                </div>)
                : (<div style={{ textAlign: "center", padding: 100 }}>
                    <CircularProgress size={100} />
                    <Typography variant="h6">CARGANDO...</Typography>
                </div>)}
        </DialogContent>);

        const facturaConDatos = data.documento_factura && data.identificacion_factura;
        var _detalle_ = [...data.detalle];
        if (data.valor_delivery) _detalle_.push({
            id: "DOMICILIO",
            pvp: data.valor_delivery,
            cantidad: 1,
            pagaIva: false
        });
        const TOTALES = _detalle_.reduce((a, x) => {
            var neto = (x.pvp * x.cantidad);
            a.cantidad += x.cantidad;
            a.neto += neto;
            if (x.pagaIva) a.iva += (neto * porcentajeIVA) / 100;
            return a;
        }, {
            cantidad: 0,
            neto: 0,
            iva: 0
        });
        TOTALES.total = (TOTALES.neto + TOTALES.iva).round(2);


        var DOMICILIO_INFO: any = [];
        if (data.mts_delivery) DOMICILIO_INFO.push(`${(data.mts_delivery / 1000).round(1)} km`);
        if (data.segs_delivery) DOMICILIO_INFO.push(`${(data.segs_delivery / 60).round(1)} min`);
        if (DOMICILIO_INFO.length) {
            DOMICILIO_INFO = DOMICILIO_INFO.join(" - ");
        } else {
            DOMICILIO_INFO = "";
        }

        const fecha = new Date(data.fechaEnvio || data.createdAt).asLocal();
        return (<>
            <DialogContent>
                <div style={{ maxWidth: 800, margin: "auto" }}>

                    <Typography style={{ textAlign: "center" }} variant="h5">ORDEN #{data.numero}</Typography>

                    <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">FECHA: </Typography>
                        <Typography style={{ display: "inline-block" }} variant="body1">{fecha}</Typography>
                    </div>
                    <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">CLIENTE: </Typography>
                        <Typography style={{ display: "inline-block" }} variant="body1">{data.nombre}</Typography>
                    </div>
                    {(data.Documento || data.identificacion) && <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">{data.Documento || "C.I. / RUC"}: </Typography>
                        <Typography style={{ display: "inline-block" }} variant="body1">{data.identificacion}</Typography>
                    </div>}
                    <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">TLF: </Typography>
                        <Typography style={{ display: "inline-block" }} variant="body1">{data.telefono}</Typography>
                    </div>
                    <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">DIRECCION: </Typography>
                        <Typography target="_blank" component="a" href={Geo.GoogleMapsRouteURL(this.props.position, [this.state.data])} style={{ display: "inline-block" }} variant="body1">{data.direccion}</Typography>
                    </div>
                    <div>
                        <Typography style={{ display: "inline-block", width: 90 }} variant="body2">REFERENCIA: </Typography>
                        <Typography style={{ display: "inline-block" }} variant="body1">{data.referencia}</Typography>
                    </div>

                    <div style={{ width: "100%", display: "flex", flexDirection: "row", marginTop: 10 }}>
                        <Typography style={{ flex: 1, marginRight: 10 }} variant="h6">PRODUCTO</Typography>
                        <Typography style={{ width: 60, marginRight: 10 }} variant="h6">CANT</Typography>
                        <Typography style={{ width: 70, textAlign: "right" }} variant="h6">TOTAL</Typography>
                    </div>

                    {data.detalle.map((item, index) => <RowDet {...item} key={index} porcentajeIVA={porcentajeIVA} />)}
                    {data.valor_delivery ? <RowDet
                        color="primary"
                        cantidad={1}
                        pvp={data.valor_delivery}
                        descripcion={"ENVIO A DOMICILIO " + DOMICILIO_INFO}
                    /> : null}

                    <div style={{ width: "100%", display: "flex", flexDirection: "row", marginTop: 5 }}>
                        <Typography variant="h6" style={{ flex: 1, marginRight: 10 }} >TOTAL</Typography>
                        <Typography style={{ marginRight: 10, width: 60 }} variant="h6">{TOTALES.cantidad.round(4)}</Typography>
                        <Typography style={{ width: 70, textAlign: "right", whiteSpace: "nowrap" }} variant="h6">$ {TOTALES.total.toFixed(2)}</Typography>
                    </div>

                    <Divider style={{ marginTop: 5, marginBottom: 20 }} />
                    <Typography>PAGO : {data.FormaPago}</Typography>
                    {data.numeroTransaccion && <Typography># de Comprobante :{data.numeroTransaccion}</Typography>}
                    <Typography>NOTAS: {data.nota}</Typography>

                    {facturaConDatos && <div>
                        <Divider style={{ marginTop: 5, marginBottom: 20 }} />
                        <Typography style={{ textAlign: "center", width: "100%" }} variant="h6">DATOS DE FACTURACIÓN</Typography>
                        <div style={{ display: "flex", flexDirection: "row" }}>
                            <Typography variant="body2">{data.Documento_factura}:</Typography>
                            <Typography variant="body2" style={{ flex: 1, marginLeft: 10 }}>{data.identificacion_factura}</Typography>
                        </div>
                        <DatosFacturaItem title="Razon Social" icon={BusinessOutlined} text={data.razonsocial_factura} />
                        <DatosFacturaItem title="Telefono" icon={PhoneOutlined} text={data.telefono_factura} />
                        <DatosFacturaItem title="Dirección" icon={LocationOnOutlined} text={data.direccion_factura} />
                    </div>}

                </div>
            </DialogContent>
            <DialogActions style={{ maxWidth: 800, width: "calc(100% - 20px)", margin: "auto" }}>
                <TextField
                    style={{ flex: 1, margin: 5 }}
                    type="phone"
                    value={phone}
                    margin="none"
                    onChange={this.handlePhone}
                    variant="standard"
                    select={true}
                    SelectProps={{
                        native: true
                    }}
                >
                    <option value="">ENVIAR POR WHATSAPP</option>
                    {contactos.map(c => <option key={c.telefono} value={c.telefono}>{c.nombre}</option>)}
                </TextField>

                <Button
                    title="Enviar por WhatsApp"
                    component="a"
                    href={OrderToPhone({ ...data, total: TOTALES.total, phone })} target="_blank"
                    variant="outlined"
                    color="primary"
                >
                    <Send />
                </Button>
            </DialogActions>
            {[0, 1].indexOf(data.estado) >= 0
                ? (<DialogActions style={{ maxWidth: 800, width: "calc(100% - 20px)", margin: "auto" }}>
                    {error && <Typography color="secondary">{error.message}</Typography>}
                    <Button disabled={!!status} onClick={this.handleClick} type="button" variant="contained" fullWidth color="primary" >
                        {NextStep(data.estado)}
                    </Button>
                </DialogActions>)
                : null}
        </>)
    }
}

export default connect(state => ({
    porcentajeIVA: state.porcentajeIVA,
    position: state.store.position
}))(OrderDetails);

function RowDet(props) {
    var neto = props.pvp * props.cantidad;
    var iva = props.pagaIva ? ((neto * props.porcentajeIVA) / 100) : 0;
    return (<div style={{ width: "100%", display: "flex", flexDirection: "row", marginTop: 5, marginBottom: 5 }}>
        <div style={{ flex: 1, marginRight: 10 }}>
            <Typography color={props.color || "default"} variant="subtitle2">{props.descripcion}</Typography>
            <Typography style={{ paddingLeft: 10 }} color={props.color || "default"} variant="body1">{props.nota}</Typography>
        </div>
        <Typography color={props.color || "default"} style={{ marginRight: 10, width: 60 }} variant="subtitle2">{props.cantidad.round(4)}</Typography>
        <Typography color={props.color || "default"} style={{ width: 70, textAlign: "right", whiteSpace: "nowrap" }} variant="subtitle2">$ {(neto + iva).round(2).toFixed(2)}</Typography>
    </div>)
}

function DatosFacturaItem(props) {
    return (
        <div style={{ display: "flex", flexDirection: "row" }} title={props.title}>
            <props.icon />
            <Typography style={{ padding: 3, flex: 1 }}>{props.text}</Typography>
        </div>
    );
}

function NextStep(step) {
    if (step === 0) return "ENVIAR A RUTA";
    if (step === 1) return "FINALIAR ENTREGA";
    return step;
}